import './App.css';
import { Text, Title, Grid, Button, TextInput, Center, List, ThemeIcon, Radio } from '@mantine/core';
import { useRef, useState, useCallback, useEffect } from 'react';
import { showNotification } from '@mantine/notifications';
import { IconCircleCheck, IconCircleDashed, IconPlus, IconSearch, IconPlayerPause, IconPlayerRecord } from '@tabler/icons';
import ReconnectingWebSocket from 'reconnecting-websocket';
const host = ''//'http://localhost:8000'
let whitelist_group = {
  public_key: "03c68f337dc3a78efd8c9801d989edbc7cd03b67381e8836cc6b81e59998f338ae",
  username: "proxy_whitelist",
  username_signature: "MEUCIQCYIJ8Ko+LUYPlOrapnB/ULCzInRTyVxLPHjNqIriVcsgIgPHmSW+qnimsA+AWDo/Omouulx+nymMtGPQ362vvgIW8=",
  wif: "L5StyB39tftmH5r83o26iUVXBQHNsgN7Mf8uyU9x9DVhHBP5vdEA"
}
let blacklist_group = {
  public_key: "02541da5105b05682013c51e09389042809d8fd16dcaaaba30a358ddf45027d1b5",
  username: "proxy_blacklist",
  username_signature: "MEQCID9KVtd8H0pMq3qCwGt6j765TX+Duip9Ujr7q7D2kRw/AiBbq8136Gk4WCuzwlZ62UXCwQGuhNH+RtJZlHSd6zoBnA==",
  wif: "L5h5LnUzSJygwxTvJP9wgRdpkJGBSuZP6UsqYqZTqH8BkLQ5SWDf",
}
let user;

/*global CenterIdentity, a*/
const ci = new CenterIdentity();
function checkDomain(nname)
{
  var arr = [
  '.com','.net','.org','.biz','.coop','.info','.museum','.name',
  '.pro','.edu','.gov','.int','.mil','.ac','.ad','.ae','.af','.ag',
  '.ai','.al','.am','.an','.ao','.aq','.ar','.as','.at','.au','.aw',
  '.az','.ba','.bb','.bd','.be','.bf','.bg','.bh','.bi','.bj','.bm',
  '.bn','.bo','.br','.bs','.bt','.bv','.bw','.by','.bz','.ca','.cc',
  '.cd','.cf','.cg','.ch','.ci','.ck','.cl','.cm','.cn','.co','.cr',
  '.cu','.cv','.cx','.cy','.cz','.de','.dj','.dk','.dm','.do','.dz',
  '.ec','.ee','.eg','.eh','.er','.es','.et','.fi','.fj','.fk','.fm',
  '.fo','.fr','.ga','.gd','.ge','.gf','.gg','.gh','.gi','.gl','.gm',
  '.gn','.gp','.gq','.gr','.gs','.gt','.gu','.gv','.gy','.hk','.hm',
  '.hn','.hr','.ht','.hu','.id','.ie','.il','.im','.in','.io','.iq',
  '.ir','.is','.it','.je','.jm','.jo','.jp','.ke','.kg','.kh','.ki',
  '.km','.kn','.kp','.kr','.kw','.ky','.kz','.la','.lb','.lc','.li',
  '.lk','.lr','.ls','.lt','.lu','.lv','.ly','.ma','.mc','.md','.mg',
  '.mh','.mk','.ml','.mm','.mn','.mo','.mp','.mq','.mr','.ms','.mt',
  '.mu','.mv','.mw','.mx','.my','.mz','.na','.nc','.ne','.nf','.ng',
  '.ni','.nl','.no','.np','.nr','.nu','.nz','.om','.pa','.pe','.pf',
  '.pg','.ph','.pk','.pl','.pm','.pn','.pr','.ps','.pt','.pw','.py',
  '.qa','.re','.ro','.rw','.ru','.sa','.sb','.sc','.sd','.se','.sg',
  '.sh','.si','.sj','.sk','.sl','.sm','.sn','.so','.sr','.st','.sv',
  '.sy','.sz','.tc','.td','.tf','.tg','.th','.tj','.tk','.tm','.tn',
  '.to','.tp','.tr','.tt','.tv','.tw','.tz','.ua','.ug','.uk','.um',
  '.us','.uy','.uz','.va','.vc','.ve','.vg','.vi','.vn','.vu','.ws',
  '.wf','.ye','.yt','.yu','.za','.zm','.zw', '.site'];
  var mai = nname;
  var val = true;
  var dot = mai.lastIndexOf(".");
  var dname = mai.substring(0,dot);
  var ext = mai.substring(dot,mai.length);
  //alert(ext);
  if(dot>2 && dot<57)
  {
    for(var i=0; i<arr.length; i++){
      if(ext === arr[i])
      {
      val = true;break;
      }else
      {
      val = false;
      }
    }
    if(val === false){
      return false;
    }else{
      for(var j=0; j<dname.length; j++){
        var dh = dname.charAt(j);
        var hh = dh.charCodeAt(0);
        if((hh > 47 && hh<59) || (hh > 64 && hh<91) || (hh > 96 && hh<123) || hh===45 || hh===46)
        {
          if((j===0 || j===dname.length-1) && hh === 45)
          {
            return false;
          }
        } else {
          return false;
        }
      }
    }
  }
  else
  {
    return false;
  }
  return true;
}
let rws
function App() {
  const [rws, setWebSocket] = useState({})
  const [webSocketEventsCalled, setWebSocketEventsCalled] = useState(false)
  if(!rws.send) {
    setWebSocket(new ReconnectingWebSocket('ws://localhost:8000/websocket'));
  }
  if(rws.addEventListener && !webSocketEventsCalled) {
    rws.addEventListener('open', async () => {
      user = await ci.createUser('user')
      whitelist_group = await ci.reviveUser(whitelist_group.wif, whitelist_group.username)
      blacklist_group = await ci.reviveUser(blacklist_group.wif, blacklist_group.username)
      rws.send(JSON.stringify({
        id: '1',
        version: 2,
        method: 'connect',
        params: {
          identity: ci.toObject(user)
        }
      }));
    });
  }
  const submitWhite = async (value, keepinput) => {
    value = value || whiteListValue
    if (!checkDomain(value)) {
      showNotification({
        title: 'Invalid domain',
        message: 'You must enter a valid domain.',
        color: 'red',
      })
      return
    }
    if (whiteListIndexed[value]) {
      if(keepinput) {
        toggleWhiteListStatus(whiteListIndexed[value])
      } else {
        showNotification({
          title: 'Domain already exists',
          message: 'You must enter a unique domain.',
          color: 'orange',
        })
        return
      }
    } else {
      let item = {
        active: true,
        domain: value
      }
      await fetch(host + '/proxy-whitelist', {
        method: 'POST',
        body: JSON.stringify(item),
        headers: {
          'Content-type': 'application/json'
        }
      })
    }
    if(!keepinput) {
      getWhiteList()
      whiteListInput.current.value = ''
      setWhiteListValue('')
      searchWhite(value)
    }
  }
  const submitBlack = async (value, keepinput) => {
    value = value || blackListValue
    if (!checkDomain(value)) {
      showNotification({
        title: 'Invalid domain',
        message: 'You must enter a valid domain.',
        color: 'red',
      })
      return
    }
    if (blackListIndexed[value]) {
      if(keepinput) {
        toggleBlackListStatus(blackListIndexed[value])
      } else {
        showNotification({
          title: 'Domain already exists',
          message: 'You must enter a unique domain.',
          color: 'orange',
        })
        return
      }
    } else {
      let item = {
        active: true,
        domain: value
      }
      await fetch(host + '/proxy-blacklist', {
        method: 'POST',
        body: JSON.stringify(item),
        headers: {
          'Content-type': 'application/json'
        }
      })
    }
    if(!keepinput) {
      getBlackList()
      blackListInput.current.value = ''
      setBlackListValue('')
      searchBlack(value)
    }
  }
  const toggleWhiteListStatus = async (item) => {
    item.active = !item.active
    await fetch(host + '/proxy-whitelist', {
      method: 'POST',
      body: JSON.stringify(item),
      headers: {
        'Content-type': 'application/json'
      }
    })
    getWhiteList()
  }
  const toggleBlackListStatus = async (item) => {
    item.active = !item.active
    await fetch(host + '/proxy-blacklist', {
      method: 'POST',
      body: JSON.stringify(item),
      headers: {
        'Content-type': 'application/json'
      }
    })
    getBlackList()
  }
  const searchWhite = async (value) => {
    value = value || whiteListValue
    const path = value && value.length ? '/proxy-whitelist?term=' + value : '/proxy-whitelist'
    const results = await fetch(host + path)
    const blackListIndexed = {}
    const list = (await results.json()).whitelist
    list.forEach(item => {
      whiteListIndexed[item.domain] = item
    });
    setWhiteList(list)
  }
  const searchBlack = async (value) => {
    value = value || blackListValue
    const path = value && value.length ? '/proxy-blacklist?term=' + value : '/proxy-blacklist'
    const results = await fetch(host + path)
    const list = (await results.json()).blacklist
    list.forEach(item => {
      blackListIndexed[item.domain] = item
    });
    setBlackList(list)
  }
  const searchRejected = async () => {
    const path = searchRejectedValue && searchRejectedValue.length ? '/proxy-rejectedlist?term=' + searchRejectedValue : '/proxy-rejectedlist'
    const results = await fetch(host + path)
    setRejectedList((await results.json()).rejectedlist)
  }
  const searchAllowed = async () => {
    const path = searchAllowedValue && searchAllowedValue.length ? '/proxy-allowedlist?term=' + searchAllowedValue : '/proxy-allowedlist'
    const results = await fetch(host + path)
    setAllowedList((await results.json()).allowedlist)
  }
  const [getWhiteListCalled, setGetWhiteListCalled] = useState(false)
  const [getBlackListCalled, setGetBlackListCalled] = useState(false)
  const [getProxyConfigCalled, setGetProxyConfigCalled] = useState(false)
  const [whiteListValue, setWhiteListValue] = useState('')
  const [blackListValue, setBlackListValue] = useState('')
  const [searchRejectedValue, setSearchRejectedValue] = useState('')
  const [searchAllowedValue, setSearchAllowedValue] = useState('')
  const [whiteList, setWhiteList] = useState([])
  const [blackList, setBlackList] = useState([])
  const [rejectedList, setRejectedList] = useState([])
  const [allowedList, setAllowedList] = useState([])
  const [proxyConfig, setProxyConfig] = useState({})
  const [whiteListIndexed, setWhiteListIndexed] = useState({})
  const [blackListIndexed, setBlackListIndexed] = useState({})
  const [rejectedListIndexed, setRejectedListIndexed] = useState({})
  const [allowedListIndexed, setAllowedListIndexed] = useState({})
  const getWhiteList = async () => {
    const result = await fetch(host + '/proxy-whitelist')
    setGetWhiteListCalled(true)
    if(whiteListInput.current?.value?.length > 0) {
      searchWhite(whiteListInput.current.value)
    } else {
      const list = (await result.json()).whitelist
      const whiteListIndexed = {}
      list.forEach(item => {
        whiteListIndexed[item.domain] = item
      });
      setWhiteList(list)
      setWhiteListIndexed(whiteListIndexed)
    }
  }
  const getBlackList = async () => {
    const result = await fetch(host + '/proxy-blacklist')
    setGetBlackListCalled(true)
    if(blackListInput.current?.value?.length > 0) {
      searchBlack(blackListInput.current.value)
    } else {
      const list = (await result.json()).blacklist
      const blackListIndexed = {}
      list.forEach(item => {
        blackListIndexed[item.domain] = item
      });
      setBlackList(list)
      setBlackListIndexed(blackListIndexed)
    }
  }
  const getProxyConfig = async () => {
    const result = await fetch(host + '/proxy-config')
    setGetProxyConfigCalled(true)
    setProxyConfig((await result.json()).proxyconfig)
  }
  !getWhiteListCalled && getWhiteList()
  !getBlackListCalled && getBlackList()
  !getProxyConfigCalled && getProxyConfig()
  const whiteListInput = useRef(null);
  const blackListInput = useRef(null);
  const [pause, setPaused] = useState({rejected: true, allowed: true})

  const updateAllowedList = (data) => {
    if (refPause.current.allowed) return
    allowedList.push(data.params)
    allowedListIndexed[data.params.domain] = data.params
    setAllowedListIndexed(allowedListIndexed)
    setAllowedList([...allowedList])
  }
  const compareFn = (a, b) => {
    if(a.domain.toLocaleLowerCase() > b.domain.toLocaleLowerCase()) return 1
    if(a.domain.toLocaleLowerCase() < b.domain.toLocaleLowerCase()) return -1
    if(a.domain.toLocaleLowerCase() === b.domain.toLocaleLowerCase()) return 0
  }

  const compareFnRejected = (a, b) => {
    if(a.timestamp > b.timestamp) return -1
    if(a.timestamp < b.timestamp) return 1
    if(a.timestamp === b.timestamp) return 0
  }

  const compareFnAllowed = (a, b) => {
    a = allowedListIndexed[a]
    b = allowedListIndexed[b]
    if(a.timestamp > b.timestamp) return -1
    if(a.timestamp < b.timestamp) return 1
    if(a.timestamp === b.timestamp) return 0
  }

  const getRejectedIcon = (item) => {
    if(
      (proxyConfig.mode === 'inclusive' && whiteListIndexed[item.domain] && whiteListIndexed[item.domain].active) ||
      (proxyConfig.mode === 'exclusive' && blackListIndexed[item.domain] && blackListIndexed[item.domain].active))
    {
      return <ThemeIcon color="teal" size={24} radius="xl">
        <IconCircleCheck size={16} />
      </ThemeIcon>
    }
    else if(
      (proxyConfig.mode === 'inclusive' && !whiteListIndexed[item.domain]) ||
      (proxyConfig.mode === 'exclusive' && !blackListIndexed[item.domain]))
    {
      return <ThemeIcon color="teal" size={24} radius="xl" style={{backgroundColor: 'red'}}>
        <IconCircleDashed size={16} />
      </ThemeIcon>
    }
    else if(
      (proxyConfig.mode === 'inclusive' && whiteListIndexed[item.domain] && !whiteListIndexed[item.domain].active) ||
      (proxyConfig.mode === 'exclusive' && blackListIndexed[item.domain] && !blackListIndexed[item.domain].active))
    {
      return <ThemeIcon color="teal" size={24} radius="xl" style={{backgroundColor: 'red'}}>
        <IconCircleDashed size={16} />
      </ThemeIcon>
    }
  }

  const getAllowedIcon = (item) => {
    if(
      (proxyConfig.mode === 'inclusive' && whiteListIndexed[item.domain] && whiteListIndexed[item.domain].active) ||
      (proxyConfig.mode === 'exclusive' && blackListIndexed[item.domain] && blackListIndexed[item.domain].active))
    {
      return <ThemeIcon color="teal" size={24} radius="xl" style={{backgroundColor: 'red'}}>
        <IconCircleCheck size={16} />
      </ThemeIcon>
    }
    else if(
      (proxyConfig.mode === 'inclusive' && !whiteListIndexed[item.domain]) ||
      (proxyConfig.mode === 'exclusive' && !blackListIndexed[item.domain]))
    {
      return <ThemeIcon color="teal" size={24} radius="xl">
        <IconCircleDashed size={16} />
      </ThemeIcon>
    }
    else if(
      (proxyConfig.mode === 'inclusive' && whiteListIndexed[item.domain] && !whiteListIndexed[item.domain].active) ||
      (proxyConfig.mode === 'exclusive' && blackListIndexed[item.domain] && !blackListIndexed[item.domain].active))
    {
      return <ThemeIcon color="teal" size={24} radius="xl">
        <IconCircleDashed size={16} />
      </ThemeIcon>
    }
  }
  const refPause = useRef(pause)
  useEffect(() => {
    refPause.current = pause;
    rws.removeEventListener('message')
    rws.addEventListener('message', async (message) => {
      const data = JSON.parse(message.data)
      if(data.method === 'connect_confirm') {
        rws.send(JSON.stringify({
          id: '1',
          version: 2,
          method: 'join_group',
          params: ci.toObject(whitelist_group)
        }));
        rws.send(JSON.stringify({
          id: '1',
          version: 2,
          method: 'join_group',
          params: ci.toObject(blacklist_group)
        }));
      }
      if(data.method === 'join_confirmed') {
        console.log(data)
      }
      if(data.method === 'new_allowed_item') {
        if (refPause.current.allowed) return
        allowedList.push(data.params)
        allowedListIndexed[data.params.domain] = data.params
        setAllowedListIndexed(allowedListIndexed)
        setAllowedList([...allowedList])
      }
      if(data.method === 'new_rejected_item') {
        if (refPause.current.rejected) return
        rejectedList.push(data.params)
        rejectedListIndexed[data.params.domain] = data.params
        setRejectedListIndexed(rejectedListIndexed)
        setRejectedList([...rejectedList])
      }
    })
    setWebSocketEventsCalled(true)
  }, [pause]);

  return (
    <div className="App">
      <Title mt="xl" order={1}>Yada Proxy Control Panel</Title>
      <Grid p="xl" m='xl'>
        <Grid.Col span={12}>
          <Text>Enter a domain (eg. google.com) then click the add button.</Text>
        </Grid.Col>
        <Grid.Col span={12}>
          <Center>
            <Radio.Group
              mt='xl'
              value={proxyConfig.mode}
              name="favoriteFramework"
              label="Proxy Mode"
              description="Inclusive means a white list is used. Exclusive means a black list will be used."
              onChange={async (value) => {
                proxyConfig.mode = value
                setProxyConfig({...proxyConfig})
                await fetch(host + '/proxy-config', {
                  method: 'POST',
                  body: JSON.stringify({
                    mode: value
                  }),
                  headers: {
                    'Content-type': 'application/json'
                  }
                })
              }}
            >
              <Radio value="inclusive" label="Inclusive" />
              <Radio value="exclusive" label="Exclusive" />
            </Radio.Group>
          </Center>
        </Grid.Col>
        {proxyConfig.mode === 'inclusive' && <Grid.Col span={6}>
          <Title mt="xl" order={1}>White list</Title>
          <Center mt="xl">
            <TextInput
              mr="sm"
              ref={whiteListInput}
              onKeyDown={(e)=>{
                if (e.nativeEvent.key === 'Enter') searchWhite()
              }}
              onChange={(e)=>{
                e.currentTarget.value = e.currentTarget.value.toLocaleLowerCase()
                setWhiteListValue(e.currentTarget.value)
              }}
            ></TextInput>
            <Button
              mr="sm"
              onClick={() => {submitWhite()}}
            >
              <IconPlus></IconPlus>
            </Button>
            <Button
              onClick={() => {searchWhite()}}
            >
              <IconSearch></IconSearch>
            </Button>
          </Center>
          <List
            style={{textAlign: 'left'}} type="ordered" mt='xl'>
            {whiteList.length > 0 && whiteList.sort(compareFn).map((item) => {
              return <List.Item
                style={{cursor: 'pointer'}}
                onClick={() => {
                  toggleWhiteListStatus(item)
                }}
                icon={
                  <ThemeIcon color="teal" size={24} radius="xl" style={item.active ? {cursor: 'pointer'} : {cursor: 'pointer', backgroundColor: 'red'}}>
                    {item.active && <IconCircleCheck size={16} />}
                    {!item.active && <IconCircleDashed size={16} />}
                  </ThemeIcon>
                }
                key={item.domain}
              >{item.domain}</List.Item>
            })}
          </List>
        </Grid.Col>}
        {proxyConfig.mode === 'exclusive' && <Grid.Col span={6}>
          <Title mt="xl" order={1}>Black list</Title>
          <Center mt="xl">
            <TextInput
              mr="sm"
              ref={blackListInput}
              onKeyDown={(e)=>{
                if (e.nativeEvent.key === 'Enter') searchBlack()
              }}
              onChange={(e)=>{
                e.currentTarget.value = e.currentTarget.value.toLocaleLowerCase()
                setBlackListValue(e.currentTarget.value.toLocaleLowerCase())
              }}
            ></TextInput>
            <Button
              mr="sm"
              onClick={() => {
                submitBlack()
              }}
            >
              <IconPlus></IconPlus>
            </Button>
            <Button
              onClick={() => {searchBlack()}}
            >
              <IconSearch></IconSearch>
            </Button>
          </Center>
          <List
            style={{textAlign: 'left'}} type="ordered" mt='xl'>
            {blackList.length > 0 && blackList.sort(compareFn).map((item) => {
              return <List.Item
                style={{cursor: 'pointer'}}
                onClick={() => {
                  toggleBlackListStatus(item)
                }}
                icon={
                  <ThemeIcon color="teal" size={24} radius="xl" style={item.active ? {cursor: 'pointer', backgroundColor: 'red'} : {cursor: 'pointer'}}>
                    {item.active && <IconCircleCheck size={16} />}
                    {!item.active && <IconCircleDashed size={16} />}
                  </ThemeIcon>
                }
                key={item.domain}
              >{item.domain}</List.Item>
            })}
          </List>
        </Grid.Col>}
        {proxyConfig.mode === 'inclusive' && <Grid.Col span={6}>
          <Title mt="xl" order={1}>Rejected list</Title>
          <Center mt="xl">
            <TextInput
              mr="sm"
              onKeyDown={(e)=>{
                if (e.nativeEvent.key === 'Enter') searchRejected()
              }}
              onChange={(e)=>{
                e.currentTarget.value = e.currentTarget.value.toLocaleLowerCase()
                setSearchRejectedValue(e.currentTarget.value.toLocaleLowerCase())
              }}
            ></TextInput>
            <Button
              onClick={() => {searchRejected()}}
            >
              <IconSearch></IconSearch>
            </Button>
            <Button
              title="Record"
              onClick={() => {
                setPaused({...pause, rejected: pause.rejected ? false : true})
              }}
            >
              {pause.rejected ? <IconPlayerRecord></IconPlayerRecord> : <IconPlayerPause></IconPlayerPause>}
            </Button>
          </Center>
          <List
            style={{textAlign: 'left'}} type="ordered" mt='xl'>
            {Object.keys(rejectedListIndexed).length > 0 && Object.keys(rejectedListIndexed).sort(compareFnRejected).map((item) => {
              item = rejectedListIndexed[item]
              return <List.Item
                key={item.host}
                style={{cursor: 'pointer'}}
                onClick={async () => {
                  setWhiteListValue(item.domain)
                  submitWhite(item.domain, true)
                  whiteListInput.current.value = item.domain
                  searchWhite(item.domain)
                }}
                icon={
                  <>
                    {getRejectedIcon(item)}
                  </>
                }
              >{item.host}</List.Item>
            })}
          </List>
        </Grid.Col>}
        {proxyConfig.mode === 'exclusive' && <Grid.Col span={6}>
          <Title mt="xl" order={1}>Allowed list</Title>
          <Center mt="xl">
            <TextInput
              mr="sm"
              onKeyDown={(e)=>{
                if (e.nativeEvent.key === 'Enter') searchAllowed()
              }}
              onChange={(e)=>{
                e.currentTarget.value = e.currentTarget.value.toLocaleLowerCase()
                setSearchAllowedValue(e.currentTarget.value.toLocaleLowerCase())
              }}
            ></TextInput>
            <Button
              mr="sm"
              onClick={() => {searchAllowed()}}
            >
              <IconSearch></IconSearch>
            </Button>
            <Button
              title="Start showing allowed traffic feed"
              onClick={() => {
                setPaused({...pause, allowed: pause.allowed ? false : true})
              }}
            >
              {pause.allowed ? <IconPlayerRecord></IconPlayerRecord> : <IconPlayerPause></IconPlayerPause>}
            </Button>
          </Center>
          <List
            style={{textAlign: 'left'}} type="ordered" mt='xl'>
            {Object.keys(allowedListIndexed).length > 0 && Object.keys(allowedListIndexed).sort(compareFnAllowed).map((item) => {
              item = allowedListIndexed[item]
              return <List.Item
                key={item.host}
                style={{cursor: 'pointer'}}
                onClick={async () => {
                  setBlackListValue(item.domain)
                  submitBlack(item.domain, true)
                  blackListInput.current.value = item.domain
                  searchBlack(item.domain)
                }}
                icon={
                  <>
                    {getAllowedIcon(item)}
                  </>
                }
              >{item.host}</List.Item>
            })}
          </List>
        </Grid.Col>}
      </Grid>
    </div>
  );
}
const styles = {
  clickable: { input: {cursor: 'pointer'}, label: {cursor: 'pointer'} }
}
export default App;
